@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body, ul, li, html, a, button, * {
  padding: 0;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  list-style: none;
  box-sizing: border-box;
  outline: none;
  text-decoration: none;
  color: #FFF;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 0 2rem;
}

:root {
  --green: #37FF4B;
  --mobile: 767px;
  --tablet: 992px;
}
main.home-page {
  width: 100%;
  min-height: 100vh;
  background-color: #232831;
  overflow: hidden;
}
nav.menu .list {
  display: flex;
  align-items: center;
  padding: 2rem 0 1rem;
  margin-bottom: 2rem;
}

nav.menu .list .item {
  margin: 0 0.5rem;
}

nav.menu .list .item a {
  color: #FFF;
  font-weight: 400;
  font-size: 16px;
  padding: 0.5rem 2rem;
  transition: .2s;
  display: flex;
  align-items: center;
  position: relative;
}

nav.menu .list .item a::before {
  content: '›';
  position: absolute;
  left: 1rem;
  opacity: 0;
  font-size: 20px;
  transition: .2s;
  font-weight: 100;
  margin-top: -2px;
  transform: translateX(-5px);
}

nav.menu .list .item a:hover {
  color: var(--green);
}

nav.menu .list .item a:hover::before {
  opacity: 1;
  transform: translateX(0px);
}

@media screen and (max-width: 767px) {
  nav.menu {
    display: none;
  }
}
section.content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 2rem;
}

section.content .left {
  max-width: 40%;
}

section.content .left .hi {
  font-size: 38px;
  font-weight: 400;
  position: relative;
  color: var(--green);
}

section.content .left .hi::before {
  content: '›';
  position: absolute;
  left: -1.5rem;
  font-size: 30px;
  transition: .2s;
  font-weight: 100;
  margin-top: 4px;
  z-index: 20;
  color: var(--green);
  animation: blink 2s infinite linear;
}

section.content .left .title {
  font-size: 28px;
  font-weight: 400;
  margin-bottom: 4rem;
}

section.content .left .title .colored {
  color: var(--green);
}

section.content .left .desc p {
  line-height: 26px;
  font-size: 16px;
  margin-bottom: 1.5rem;
}

section.content .right {
  max-width: 50%;
  width: 100%;
  position: relative;
}

section.content .right .laptop {
  width: 500px;
  margin-top: 10rem;
  margin-left: 10rem;
  animation: fade-top 1s ease-in-out;
}

section.content .right .phone {
  position: absolute;
  left: 5%;
  top: 20%;
  width: 150px;
  animation: fade-top 1s ease-in-out;
}

@keyframes fade-top {
  0% {
    opacity: 0;
    transform: translate(-5px, 30px);
  }

  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0%, 49% {
    opacity: 1;
  }

  50%, 100% {
    opacity: 0;
  }
}

@media screen and (max-width: 767px) {
  section.content .left {
    max-width: 100%;
  }

  section.content .right { display: none; }
}
